export default {
	props: ["userInfo", "serviceList", "currentOrder"],
	name: "basics",
	data() {
		return {
			workStatus: 0,  // 0还没确定 1已经确定
			orderMethod: 0,
			filterMethod: 0,
			modalName: null,
			cardCur: 0,
			swiperList: [{
				id: 0,
				type: 'image',
				url: '../../static/girls/1.jpg'
			}, {
				id: 1,
				type: 'image',
				url: '../../static/girls/2.jpg',
			}, {
				id: 2,
				type: 'image',
				url: '../../static/girls/3.jpg'
			}],
			dotStyle: false,
		};
	},
	mounted(){
		console.log(this.$props.currentOrder);
	},
	methods:{
		selectServiceAdd(){
			this.modalName = null
			uni.showModal({
				title: '',
				content: '确定加钟该项目？（选择后无法改变）',
				success: (res) => {
					if(res.confirm){
						this.workStatus = 1
					} else {
						this.modalName = 'serviceAdd'
					}
				}
			})
		},
		changeBusyStatus(status){
			console.log(this.$props.currentOrder);
			var targetStatus = !this.$props.userInfo.is_free
			var data = {
				id: this.$props.currentOrder == undefined ? undefined : this.$props.currentOrder.id,
				targetStatus, 
				workStatus: this.workStatus
			}
			this.$emit("CancelOrder", data)
		},
		selectService(index){
			this.modalName = null
			uni.showModal({
				title: '',
				content: '确定选择该项目？（选择后无法改变）',
				success: (res) => {
					if(res.confirm){
						this.workStatus = 1
					} else {
						this.modalName = 'service'
					}
				}
			})
		},
		goSearch(){
			uni.navigateTo({
				url: '../search/search'
			})
		},
		book(e){
			this.modalName = null
			uni.navigateTo({
				url: "../people-detail/people-detail?status=" + e.currentTarget.dataset.key
			})
		},
		viewDetail(){
			this.modalName = "book"
		},
		hideModal(){
			this.modalName = null
		},
		changeModal(e){
			this.modalName = e.currentTarget.dataset.name
		},
		showLocation(){
			
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
	},
	onShow() {
		console.log("success")
	}
}
